*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
main{
    font-family: "Heebo",sans-serif;
}

.landing{
    min-height: 100vh;
    background: url("./car.jpg");
    background-size:cover;
    padding:0rem 5rem;
}
nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width: 10vh;
}

.nav-links{
    display:flex;
    list-style:none;
}
.nav-links li{
    padding-left: 10rem;
    font-size:1.2rem;
}

#logo{
    font-family: "Lobster",cursive;
    font-weight: lighter;
    font-size: 2rem; /*32px*/
}

.big-text{
    position:absolute;
    top:30%;
    left: 50%;
    transform:translate(-50%,-30%);
    font-size:5rem;
    font-family: "Lobster",cursive;
    color:rgb(61,61,61)
}

.intro{
    background: black;
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    display:flex;
    justify-content: center;
    align-items: center;    

}

.intro-text{
    color:rgb(122, 134, 139);
    font-family:"Heebo",sans-serif;
    font-size:3rem;
}

.slider{
    background: rgb(97, 105, 109);
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    transform: translateY(100%);
}

.hide{
    background: black;
    overflow: hidden;
}

.hide span{
    transform: translateY(100%);
    display: inline-block;
}

.middle{
   transform: translateY(-100%);
}